import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { addItem, removeProduct } from "../redux/action";
import { useDispatch, useSelector } from "react-redux";
import Loader from "../components/Loader";
import dataJSON from "../data/data.json"

const Product = () => {
  const dispatch = useDispatch();
  const { id } = useParams();
  const [product, setProduct] = useState([]);
  const [loading, setLoading] = useState(true);
  const [imgSrc, setImgSrc] = useState("");
  const [productStatus, setProductStatus] = useState(
    useSelector((state) => state.handleCart).some((x) => x.id === id)
  );

  useEffect(() => {
    const getProduct = () => {
      const response = [...dataJSON].find(el => el.id == id);
      setProduct(response);
    };
    getProduct();
  }, []);

  useEffect(() => {
    import(`../assets/productImgs/${product.image}`).then(image => {
      setImgSrc(image.default);
    });
    setLoading(false);
  }, [product])

  const handleCartBtn = () => {
    productStatus
      ? dispatch(removeProduct(product))
      : dispatch(addItem(product));
    setProductStatus(!productStatus);
  };

  const ShowProduct = () => {
    return (
      <>
        <div className="col-md-6 d-flex justify-content-center align-items-center">
          <div className="position-relative w-100 pt-100">
            <div className="position-absolute top-0 bottom-0 w-100 h-100">
              <img
                src={imgSrc}
                alt={product?.title}
                dataName={product.image}
                className="card-img-top img-contain"
              />
            </div>
          </div>
        </div>
        <div className="col-md-6 pt-3 pt-md-0">
          <h4 className="text-uppercase text-black-50">{product?.category}</h4>
          <h1 className="display-5">{product?.title}</h1>
          <h3 className="display-6 fw-bold my-4">$ {product?.price}</h3>
          <p className="lead">{product?.description}</p>
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className={`btn btn-outline-dark px-4 py-2${productStatus ? " active" : ""
                }`}
              onClick={handleCartBtn}
            >
              {productStatus ? "Remove from Cart" : "Add to Cart"}
            </button>
            <Link className="btn btn-dark ms-2 px-3" to={"/cart"}>
              Go to Cart
            </Link>
          </div>
        </div>
      </>
    );
  };

  return (
    <div className="container">
      <div className="row py-4">{loading ? <Loader /> : <ShowProduct />}</div>
    </div>
  );
};

export default Product;
