import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Loader from '../components/Loader';
import Table from './Table';
import { useSelector } from "react-redux";
import { Navigate } from 'react-router-dom';

const Statistics = () => {
  const [data, setData] = useState("");
  const sessionId = useSelector((state) => state.handleSession.sessionId);
  const admin = useSelector((state) => state.handleSession.admin);

  useState(() => {
    axios.get('http://shop/getStatistics.php', {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
      .then(response => {
        console.log(response.data);
        setData(response.data);
      })
      .catch(error => {
        console.error('There was an error fetching the data!', error);
      });
  }, []);

  if (!admin) return <Navigate to="/" />;
  if (!data) return <Loader />;
  return (
    <>
      <Table data={data.Customers} title="Customers" />
      <Table data={data.Orders} title="Orders" />
      <Table data={data.Services} title="Services" />
      <Table data={data.OrderDetails} title="Order Details" />
      <Table data={data.Employees} title="Employees" />
      <Table data={data.Assignments} title="Assignments" />
      <Table data={data.Payments} title="Payments" />
      <Table data={data.Inventory} title="Inventory" />
      <Table data={data.Suppliers} title="Suppliers" />
      <Table data={data.Feedback} title="Feedback" />
    </>
  );
};

export default Statistics;